// lib/common/util.js
function setHTML(el, html, delay = 0) {
  return new Promise(function(resolve, reject) {
    if (html) {
      setTimeout(function() {
        const slotExists = html.indexOf("</slot>");
        if (slotExists) {
          const templateHTML = html.replace("<slot></slot>", el.innerHTML);
          el.innerHTML = templateHTML;
        } else {
          el.innerHTML = html;
        }
        executeScript(el);
        resolve(true);
      }, delay);
    } else {
      resolve(false);
    }
  });
}
function executeScript(el) {
  el.querySelectorAll("script").forEach((old) => {
    const scriptEl = document.createElement("script");
    Array.from(old.attributes).forEach((attr) => scriptEl.setAttribute(attr.name, attr.value));
    scriptEl.appendChild(document.createTextNode(old.innerText));
    try {
      el.replaceChild(scriptEl, old);
    } catch (e) {
      el.appendChild(scriptEl);
    }
  });
}
function waitUntil(condition) {
  if (Number.isInteger(condition)) {
    return new Promise(function(resolve) {
      setTimeout((_) => resolve(), condition);
    });
  } else if (typeof condition === "string") {
    let waited = 0;
    return new Promise(function(resolve, reject) {
      function waitForCondition() {
        if (waited > 3e3)
          reject();
        if (window[condition])
          return resolve(window[condition]);
        setTimeout(waitForCondition, 100);
        waited += 100;
      }
      waitForCondition();
    });
  } else if (typeof condition === "function") {
    let waited = 0;
    return new Promise(function(resolve, reject) {
      function waitForCondition() {
        if (waited > 3e3)
          reject();
        if (condition())
          return resolve(condition());
        setTimeout(waitForCondition, 100);
        waited += 100;
      }
      waitForCondition();
    });
  }
}
function define(dfltTagName, dfltKlass) {
  return function(tagName, klass) {
    tagName = tagName || dfltTagName;
    klass = klass || dfltKlass;
    if (!customElements.get(tagName)) {
      customElements.define(tagName, klass);
    }
  };
}

// lib/qrcode/qrcodejs.html
var qrcodejs_default = '<script id="x-qrcode" src="//unpkg.com/qrcode@1.4.4/build/qrcode.min.js"><\/script>';

// lib/qrcode/index.js
var XQRCode = class extends HTMLElement {
  connectedCallback() {
    this.value = this.getAttribute("value") || "Hello QR Code";
    const scriptEl = document.querySelector("script#x-qrcode");
    const thisHtml = scriptEl ? "" : qrcodejs_default;
    setHTML(this, thisHtml).then((_) => waitUntil("QRCode")).then((_) => QRCode.toDataURL(this.value)).then((url) => {
      const img = document.createElement("img");
      img.setAttribute("alt", this.value);
      img.setAttribute("title", this.value);
      img.src = url;
      this.appendChild(img);
    }).catch((err) => {
    });
  }
};
XQRCode.define = define("x-qrcode", XQRCode);
export {
  XQRCode
};
